// box-inof
.box-inof {
  width: 25em;
  height: 15em;
  background: #00FF00;
  border-radius: 10px;
  padding: 1em;
  position: absolute;
  box-shadow: 2px 2px 5px #494848;
  clip-path: circle(6% at 50% 10%);
  transition: clip-path 450ms ease-in, background 450ms ease-in;
}

.box-inof .mark {
  content: "?";
  font-family: Segoe UI;
  font-weight: 900;
  font-size: 2em;
  position: absolute;
  top: .1em;
  left: 48.5%;
  width: 1rem;
  height: 1rem;
  color: rgba(94, 94, 94, 1);
  transition: color 450ms linear;
  background: none;
}

.box-inof:hover .mark {
  color: rgba(94, 94, 94, 0);
}

.box-inof:hover {
  clip-path: circle(80%);
  background: #f0f0f0;
}

.box-inof .title {
  font-family: Segoe UI;
  margin-top: 0px;
  margin-bottom: 1em;
}

.box-inof .content {
  font-family: Segoe UI;
  line-height: 1.7em;
  color: #5e5e5e;
  font-weight: 550;
}
